import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { FormValidationModule } from 'src/app/shared/form-validation/form-validation.module';
import { NgSelectModule } from '@ng-select/ng-select';
import { SharedModule } from 'src/app/shared/shared.module';
import { AddPutAwayComponent } from './add-put-away.component';
import { PutAwayService } from 'src/app/service/put-away.service';

const routes: Routes = [
  {
    path: '',
    component: AddPutAwayComponent
  },
  {
    path: ':id',
    component: AddPutAwayComponent
  }
]

@NgModule({
  declarations: [AddPutAwayComponent],
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    FormValidationModule,
    NgSelectModule,
    SharedModule,
    RouterModule.forChild(routes)
  ], providers: [PutAwayService]
})

export class AddPutAwayModule { }

